import React, {useContext} from "react";
import { Link } from "react-router-dom"
import {useCookies} from 'react-cookie'
import ProductContext from "./Context/ProductContext";
import ScrollTopAction from "./Action/ScrollTopAction";

function Header(props){
  let [cookie, setCookie] = useCookies(['cart'])
  let {state, action} = useContext(ProductContext);

  let showbar = ()=>{
    document.getElementById('nav-menu').classList.remove('translate-x-full');
    document.body.style.overflowY = 'hidden';
  }
  let toggle = (e)=>{
    e.target.querySelector('span').classList.toggle('rotate-180');
    e.target.querySelector('div').classList.toggle('hidden');
  }
  let close = ()=>{
    document.getElementById('nav-menu').classList.add("translate-x-full");
    document.body.style.overflowY = 'auto';
  }
  let hidebar = ()=>{
    document.getElementById('nav-menu').classList.add("translate-x-full");
    document.body.style.overflowY = 'auto';
    ScrollTopAction();
    
  }
  let women = state.cat.women.map((item, key)=><Link key={key} to={`/cat/women/${item}`} onClick={ScrollTopAction} className="py-4 px-2 overflow-hidden text-ellipsis whitespace-nowrap text-center w-full hover:bg-gray-500">{item}</Link>);
  let men = state.cat.men.map((item, key)=><Link key={key} to={`/cat/men/${item}`} onClick={ScrollTopAction} className="py-4 px-2 overflow-hidden text-ellipsis whitespace-nowrap text-center w-full hover:bg-gray-500">{item}</Link>);
  let womenside = state.cat.women.map((item, key)=><Link key={key} to={`/cat/women/${item}`} onClick={hidebar} className="py-4 px-2 overflow-hidden text-ellipsis whitespace-nowrap text-center w-full hover:bg-gray-500 block transition-all duration-200 rounded">{item}</Link>);
  let menside = state.cat.men.map((item, key)=><Link key={key} to={`/cat/men/${item}`} onClick={hidebar} className="py-4 px-2 overflow-hidden text-ellipsis whitespace-nowrap text-center w-full hover:bg-gray-500 block transition-all duration-200 rounded">{item}</Link>);
return (
  
<>
    <div className="sticky top-0 left-0 lg:relative z-30">
    <div className="flex flex-row-reverse justify-between shadow bg-white">
      <div className="hidden flex-row-reverse lg:flex uppercase">
        <Link to='/' onClick={ScrollTopAction} className="my-2 mx-2 px-4 py-2 hover:bg-gray-600 hover:text-white rounded" >Home</Link>
        <div className="menu-down my-2 py-2 hover:bg-gray-600 hover:text-white rounded-t relative"><Link className="px-4 py-2 " to={`/cat/women`} onClick={ScrollTopAction}>Women Cat</Link>
          <div className="overflow-hidden flex-col justify-between items-center absolute top-full left-0 w-full bg-gray-600 rounded-b text-white hidden opacity-0">
            <span className="py-1"></span>
            {women}
          </div>
        </div>
        <div className="menu-down my-2 py-2 hover:bg-gray-600 hover:text-white rounded-t relative"><Link className="px-6 py-2 " to={`/cat/men`} onClick={ScrollTopAction}>Men Cat</Link>
          <div className="overflow-hidden flex-col justify-between items-center absolute top-full left-0 w-full bg-gray-600 rounded-b text-white hidden opacity-0">
            <span className="py-1"></span>
            {men}
          </div>
        </div>
        <Link to={`/about_us`} onClick={ScrollTopAction} className="my-2 mx-2 px-4 py-2 hover:bg-gray-600 hover:text-white rounded">About Us</Link>
        <Link to={`/contact_us`} onClick={ScrollTopAction} className="my-2 mx-2 px-4 py-2 hover:bg-gray-600 hover:text-white rounded">Contact Us</Link>
    </div>
    <div className="flex flex-row-reverse lg:hidden">
      <span id="menu" onClick={showbar} className="px-4 py-2 text-gray-600 hover:text-gray-700 transition-all duration-100">
        <span className="material-icons text-3xl">
        menu
        </span>
        </span>
    </div>

      <div className="flex items-center ml-2">
        <Link to='/cart' onClick={ScrollTopAction} className="px-2 py-2 mx-1 text-gray-600 hover:text-gray-700 transition-all duration-100 relative"><span className="material-icons text-2xl">
          shopping_cart
          
          </span>
          {cookie.cart ?
                    <div className=" absolute top-[3%] left-[68%] rounded-full bg-gray-500 text-white h-6 w-6 flex justify-center items-center">{cookie.cart.length}</div>
                      :
                      ''
                      }
          </Link>
          <div className="flex flex-row-reverse mx-1">
            <Link to='/search/' onClick={ScrollTopAction} className="px-2 cursor-pointer py-2 text-gray-600 hover:text-gray-700 transition-all duration-100"><span className="material-icons text-2xl">
              search
              </span></Link>

          </div>
      </div>
    </div>
    </div>
    <div id="nav-menu" className="w-full h-screen fixed py-10 pt-12 top-0 right-0 bg-gray-800/90 z-50 translate-x-full transition-all duration-300 flex flex-col items-center justify-center">
      <span id="close-menu" onClick={close} className="flex absolute top-4 right-7 text-white scale-[3.5] rotate-45">+</span>
      <div className="flex w-full h-full items-center justify-center">
        <div className="flex flex-col items-center overflow-y-auto  overflow-x-hidden h-[98%] w-full">
        <Link to='/' onClick={hidebar} className="text-center w-full mx-2 px-4 py-4 text-white rounded transition-all duration-100">Home</Link>
          <div onClick={toggle} className="mx-2 px-4 py-4 flex flex-col items-center text-white rounded transition-all duration-100 w-full">Women <span className="material-icons text-white align-middle transition-all duration-200">
            expand_more
            </span>
            <div className="flex-col w-full items-center justify-between text-white flex hidden bg-gray-600 p-4 rounded">
              {womenside}
            </div>
          </div>
          <div onClick={toggle} className="mx-2 px-4 py-4 flex flex-col items-center text-white rounded transition-all duration-100 w-full">Men <span className="material-icons text-white align-middle transition-all duration-200">
            expand_more
            </span>
            <div className="flex-col w-full items-center justify-between text-white flex hidden bg-gray-600 p-4 rounded">
              {menside}
            </div>
          </div>
          <Link to={`/about_us`} onClick={hidebar} className="text-center w-full mx-2 px-4 py-4 text-white rounded transition-all duration-100">About Us</Link>
          <Link to={`/contact_us`} onClick={hidebar} className="text-center w-full mx-2 px-4 py-4 text-white rounded transition-all duration-100">Contact Us</Link>
      </div>
      </div>
    </div>
  </>
);
}

export default Header;